  display: block;
}
body {
  line-height: 1;
  font-family: 'Hiragino Sans GB', 'Microsoft YaHei', simsun;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
body {
  margin: 0;
  font-size: .22rem;
  -webkit-tap-highlight-color: transparent;
  width: 6.4rem;
  color: #333;
  margin: 0 auto;
}
a {
  color: #666;
  text-decoration: none;
  -webkit-tap-highlight-color: transparent;
}
input {
  -webkit-appearance: none;
  outline: none;
  border-radius: 0;
  border: none;
}
.btn {
  display: inline-block;
  border-radius: .05rem;
  text-align: center;
}
.btn.btn-small {
  padding: .1rem .2rem;
}
.btn.btn-middle {
  padding: .2rem .4rem;
}
.btn.btn-big {
  padding: .3rem .8rem;
}
.btn.normal {
  background: #5BCE8C;
  color: #fff;
}
.btn.normal:active {
  background: #439667;
}
.btn.warnn {
  background: #feee1c;
}
.btn.btn-block {
  display: block;
}
.btn.btn-center {
  margin: 0 auto;
}
.loading {
  display: block;
  margin: .5rem auto;
  width: .4rem;
  height: .39rem;
  background: url(../../assets/icon-loading.png) no-repeat;
  background-size: 100%;
  -webkit-animation: rotate 2s infinite linear;
          animation: rotate 2s infinite linear;
}
@-webkit-keyframes rotate {
0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes rotate {
0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
.text-l {
  text-align: left;
}
.text-r {
  text-align: right;
}
.text-c {
  text-align: center;
}
.center {
  margin: 0 auto;
}
.norm-header {
  position: relative;
  height: .8rem;
  line-height: .8rem;
  color: #333;
  border-bottom: .015rem solid #ddd;
}
.norm-header .back {
  position: absolute;
  color: #333;
  left: .2rem;
}
.norm-header h3 {
  text-align: center;
  font-weight: normal;
  font-size: .28rem;
}
.loginwrap {
  margin-top: 1rem;
  padding: 0 5%;
}
.loginwrap h3 {
  font-size: .34rem;
  text-align: center;
  margin-bottom: .7rem;
}
.loginwrap .formgroup {
  padding: 0 .4rem;
  margin-bottom: .2rem;
}
.loginwrap .formgroup input {
  display: block;
  width: 100%;
  box-sizing: border-box;
  height: .5rem;
  line-height: .5rem;
  padding: 0 .1rem;
  font-size: .24rem;
  border: none;
  border-bottom: .015rem solid #ddd;
  transition: all .3s;
}
.loginwrap .formgroup input:focus {
  border-color: #333;
}
.loginwrap .formgroup p {
  width: 100%;
  color: red;
  font-size: .2rem;
  margin-top: .1rem;
}
.loginwrap .formgroup p.text-r,
.loginwrap .formgroup p.text-l {
  margin-top: .3rem;
}
.loginwrap .formgroup p.text-r a,
.loginwrap .formgroup p.text-l a {
  font-size: .25rem;
  color: #333;
}
.loginwrap .btn {
  display: block;
  width: 2rem;
  height: .6rem;
  line-height: .6rem;
  color: #fff;
  background: #5bce8c;
  border-radius: .05rem;
  text-align: center;
  margin: .5rem auto;
}
.loginwrap .btn:active {
  background: #439667;
}
.group {
  border-top: .015rem solid #eee;
  border-bottom: .015rem solid #eee;
  background: #fff;
  margin-bottom: .2rem;
}
.group .cell {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  min-height: .8rem;
  border-bottom: .015rem solid #eee;
  margin: 0 .3rem;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.group .cell > span {
  width: 1rem;
  line-height: .8rem;
}
.group .cell:last-child {
  border-bottom: none;
}
.group.notborder {
  border-top: none;
}



