
#changeuserinfo .text {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  height: .4rem;
  font-size: .22rem;
}
#changeuserinfo .cell .img {
  margin: .2rem 0;
  position: relative;
  width: 1.4rem;
  height: 1.4rem;
  border: .015rem solid #e5e5e5;
  border-radius: 50%;
}
#changeuserinfo .cell .img img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
#changeuserinfo .cell .img .file {
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0;
  z-index: 2;
}
#changeuserinfo .cell .img .progress {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.4);
}
#changeuserinfo .cell .img .progress .progress-bar {
  position: absolute;
  height: .04rem;
  background: #F00;
  left: 0;
  bottom: 0;
}
#changeuserinfo .btn-middle {
  margin-top: .4rem;
}
.showFromTop-enter-active {
  transition: all .3s ease;
}
.showFromTop-leave-active {
  transition: all 0.3s ease-out;
}
.showFromTop-enter,
.showFromTop-leave-active {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}



