
body > div:not(#app) {
  display: none;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.weui-mask_transparent {
  position: fixed;
  z-index: 1000;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}
.weui-toast {
  position: fixed;
  z-index: 5000;
  width: 9em;
  min-height: 9em;
  top: 270px;
  left: 50%;
  margin-left: -3.8em;
  background: rgba(17, 17, 17, 0.7);
  text-align: center;
  border-radius: 5px;
  color: #FFFFFF;
}
.weui-icon_toast {
  margin: 45px 0 0;
  display: block;
}
.weui-icon-success {
  width: .5rem;
  height: .5rem;
  background: url(assets/success.png) no-repeat;
  margin: 35px auto 0;
  background-size: 100%;
}
.weui-toast__content {
  margin: 0 0 15px;
}
.fadeIn-enter-active,
.fadeIn-leave-active {
  transition: all .3s ease;
}
.fadeIn-enter,
.fadeIn-leave-active {
  opacity: 0;
}



