
#listen-box {
  z-index: 2;
  position: fixed;
  width: 6.4rem;
  height: 1.2rem;
  bottom: 0;
  box-sizing: border-box;
  padding: .1rem;
  transition: all .3s;
}
#listen-box .minibox {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 2;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
#listen-box .minibox img {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  transition: all .3s;
}
#listen-box .minibox .info {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  color: #fff;
}
#listen-box .minibox .info p {
  text-align: center;
  font-size: .24rem;
  margin-bottom: .2rem;
}
#listen-box .minibox .info .time {
  height: .3rem;
  line-height: .3rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 0 .5rem;
}
#listen-box .minibox .info .time span {
  text-shadow: 0.02rem 0.02rem 0.04rem rgba(0, 0, 0, 0.6);
}
#listen-box .minibox .info .time .time-bar-wrap {
  position: relative;
  min-width: 70%;
  height: .08rem;
  background: #ccc;
  margin: .08rem .2rem;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
#listen-box .minibox .info .time .time-bar-wrap .time-bar {
  position: absolute;
  left: 0;
  height: 100%;
  background: #5dc78a;
}
#listen-box .minibox .info .time .time-bar-wrap .time-bar:before {
  content: ' ';
  position: absolute;
  display: block;
  width: .14rem;
  height: .14rem;
  background: #fff;
  border-radius: 50%;
  right: -0.07rem;
  top: -0.03rem;
}
#listen-box .minibox > span {
  display: block;
  width: .48rem;
  height: .48rem;
  background: url(../assets/icon-start.png) no-repeat;
  background-size: 100%;
  margin-right: .2rem;
  transition: all .2s;
}
#listen-box .minibox > span:active {
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}
#listen-box .minibox .header {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background: none;
  height: .9rem;
}
#listen-box .minibox .header .back {
  position: absolute;
  display: block;
  z-index: 2;
  left: .1rem;
  top: .235rem;
  width: .12rem;
  height: .23rem;
  padding: .1rem;
  background: url(../assets/icon-back.png) center center no-repeat;
  background-size: .12rem .23rem;
}
#listen-box .minibox .function-bar {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1.2rem;
  box-sizing: border-box;
  padding-top: .42rem;
  text-align: center;
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
  transition: all .3s;
}
#listen-box .minibox .function-bar a {
  display: inline-block;
  width: .35rem;
  height: .35rem;
  background-size: 100%;
  margin: 0 .2rem;
}
#listen-box .minibox .function-bar a.prev {
  background-image: url(../assets/icon-prev.png);
}
#listen-box .minibox .function-bar a.state {
  background-image: url(../assets/icon-start1.png);
}
#listen-box .minibox .function-bar a.next {
  background-image: url(../assets/icon-next.png);
}
#listen-box .minibox .skinBox {
  display: none;
  position: absolute;
  bottom: 2.4rem;
  right: .3rem;
}
#listen-box .minibox .skinBox > a {
  display: block;
  width: .54rem;
  height: .54rem;
}
#listen-box .minibox .skinBox > a.blue {
  background: url(../assets/195db1.png);
  background-size: 100%;
}
#listen-box .minibox .skinBox > a.red {
  background: url(../assets/d81e02.png);
  background-size: 100%;
}
#listen-box .minibox .skinBox > a.black {
  background: url(../assets/000000.png);
  background-size: 100%;
}
#listen-box .minibox .skinBox > a.green {
  background: url(../assets/1b5e20.png);
  background-size: 100%;
}
#listen-box .minibox .skinBox .skinwrap {
  position: absolute;
  top: -1.9rem;
  right: .06rem;
  visibility: hidden;
}
#listen-box .minibox .skinBox .skinwrap span {
  display: block;
  width: .35rem;
  height: .35rem;
  border: .02rem solid #fff;
  border-radius: 50%;
  margin-bottom: .1rem;
  transition: all .2s linear;
  opacity: 0;
  -webkit-transform: translateX(0.12rem);
          transform: translateX(0.12rem);
}
#listen-box .minibox .skinBox .skinwrap span:nth-child(1) {
  background: #195db1;
  transition-delay: 0s;
}
#listen-box .minibox .skinBox .skinwrap span:nth-child(2) {
  background: #d81e02;
  transition-delay: .05s;
}
#listen-box .minibox .skinBox .skinwrap span:nth-child(3) {
  background: #000000;
  transition-delay: .1s;
}
#listen-box .minibox .skinBox .skinwrap span:nth-child(4) {
  background: #1b5e20;
  transition-delay: .15s;
}
#listen-box .minibox .skinBox .skinwrap.active {
  visibility: visible;
}
#listen-box .minibox .skinBox .skinwrap.active span {
  -webkit-transform: translateX(0);
          transform: translateX(0);
  opacity: 1;
}
#listen-box .bigbox {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  overflow: hidden;
}
#listen-box .bigbox .bg {
  overflow: hidden;
  width: 100%;
  height: 100%;
  -webkit-filter: blur(10px);
          filter: blur(10px);
  -webkit-transform: scale(1.4);
          transform: scale(1.4);
  background-size: cover;
  background-position: center center;
}
#listen-box .bigbox .rotateImg {
  position: absolute;
  top: 50%;
  margin-top: -2.5rem;
  left: 50%;
  margin-left: -1.725rem;
  width: 3.5rem;
  height: 3.5rem;
  border-radius: 50%;
  background-size: 100%;
}
#listen-box .bigbox .rotateImg.active {
  -webkit-animation: rotate 10s linear infinite;
          animation: rotate 10s linear infinite;
}
#listen-box.active .minibox img {
  -webkit-animation: rotate 10s infinite linear;
          animation: rotate 10s infinite linear;
}
#listen-box.active .minibox > span {
  background-image: url(../assets/icon-stop.png);
}
#listen-box.active .minibox .function-bar a.state {
  background-image: url(../assets/icon-stop2.png);
}
#listen-box.big-box {
  padding: 0;
  height: 100%;
}
#listen-box.big-box .minibox {
  position: relative;
  z-index: 2;
}
#listen-box.big-box .minibox img {
  display: none;
}
#listen-box.big-box .minibox .info {
  transition: all .3s;
}
#listen-box.big-box .minibox .info p {
  position: absolute;
  top: 0;
  left: 0;
  font-size: .28rem;
  width: 100%;
  line-height: .9rem;
  transition: all .3s;
}
#listen-box.big-box .minibox .info .time {
  position: absolute;
  width: 90%;
  bottom: 1.5rem;
  padding: 0 5%;
  transition: all .3s;
}
#listen-box.big-box .minibox .info .time .time-bar-wrap {
  margin-top: .12rem;
  transition: all .3s;
}
#listen-box.big-box .minibox > span {
  display: none;
}
#listen-box.big-box .minibox .header {
  display: block;
}
#listen-box.big-box .minibox .function-bar {
  -webkit-transform: translateY(0);
          transform: translateY(0);
}
#listen-box.big-box .minibox .skinBox {
  display: block;
}
.trans-fade-enter-active,
.trans-fade-leave {
  transition: all .3s;
  -webkit-transform: translateY(0);
          transform: translateY(0);
}
.trans-fade-enter,
.trans-fade-leave-active {
  transition: all .3s;
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
}
@-webkit-keyframes rotate {
0 {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes rotate {
0 {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}



